<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.search" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getRadios" v-if="login_user.manage_level_id == 1"></el-button>
                    <el-button type="primary" v-on:click="getRadios" disabled v-else></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd" v-if="login_user.manage_level_id == 1"></el-button>
                    <el-button type="primary" @click="handleAdd" disabled v-else></el-button>
                </el-form-item>
            </el-form>
        </el-col>
    
        <!---->
        <el-table :data="radios" border highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column type="index" width="60">
            </el-table-column>
            <el-table-column prop="title" label="" width="120" sortable>
            </el-table-column>
            <el-table-column prop="author" label="" width="120" sortable>
            </el-table-column>
            <el-table-column prop="url" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="duration" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="upload_time" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="praise_number" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="play_number" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="img_url" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="article_author" label="" min-width="120" sortable>
            </el-table-column>
            <!-- <el-table-column prop="article_content" label="" min-width="120" sortable>
            </el-table-column> -->
            <el-table-column prop="article_remark" label="" min-width="120" sortable>
            </el-table-column>

            <el-table-column prop="created_at" label="" width="120" sortable>
            </el-table-column>
            <el-table-column fixed="right" label="" width="150">
                <template scope="scope">
                    <el-button size="small" @click="handleEdit(scope.row)" v-if="login_user.manage_level_id == 1"></el-button>
                    <el-button size="small" @click="handleEdit(scope.row)" disabled v-else></el-button>
                    <el-button type="danger" size="small" @click="handleDel(scope.row)" v-if="login_user.manage_level_id == 1"></el-button>
                    <el-button type="danger" size="small" @click="handleDel(scope.row)" disabled v-else></el-button>
                </template>
            </el-table-column>
        </el-table>
    
        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
    
        <!---->
        <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
            <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
                <el-form-item label="">
                    <el-input v-model="editForm.title"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editForm.author"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" autosize v-model="editForm.url"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editForm.duration"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editForm.upload_time"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editForm.praise_number"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editForm.play_number"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" autosize v-model="editForm.img_url"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" autosize v-model="editForm.article_author"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" autosize v-model="editForm.article_content"></el-input>
                </el-form-item>
                <el-form-item type="textarea" autosize label="">
                    <el-input v-model="editForm.article_remark"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
        </el-dialog>
    
        <!---->
        <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
            <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
                <el-form-item label="">
                    <el-input v-model="addForm.title"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.author"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" autosize v-model="addForm.url"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.duration"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.upload_time"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.praise_number"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.play_number"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" autosize v-model="addForm.img_url"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" autosize v-model="addForm.article_author"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" autosize v-model="addForm.article_content"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" autosize v-model="addForm.article_remark"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
import util from "../../common/js/util";
import {
  getRadiosList,
  removeRadio,
  batchRemoveRadio,
  editRadio,
  addRadio
} from "../../api/api";

export default {
  data() {
    return {
        login_user: {},
    
        filters: {
            search: ""
        },
        radios: [],
        total: 0,
        page: 1,
        listLoading: false,
        sels: [], //

        editFormVisible: false, //
        editLoading: false,
        editFormRules: {
            // content: [{ required: true, message: "", trigger: "blur" }]
        },
        //
        editForm: {},

        addFormVisible: false, //
        addLoading: false,
        addFormRules: {
            // content: [{ required: true, message: "", trigger: "blur" }]
        },
        //
        addForm: {}
    };
  },
  methods: {
    //
    // formatAnonymous: function(row, column) {
    //   return row.anonymous == 1 ? "" : row.anonymous == 0 ? "" : "";
    // },
    // formatAvailable: function(row, column) {
    //   return row.available == 1 ? "" : row.available == 0 ? "" : "";
    // },
    handleCurrentChange(val) {
      this.page = val;
      this.getRadios();
    },
    //
    getRadios() {
      let para = {
        page: this.page,
        search: this.filters.search
      };
      this.listLoading = true;
      getRadiosList(para).then(res => {
        console.log("getRadiosList res", res);
        this.total = res.data.dataLength;
        this.radios = res.data.data;
        this.listLoading = false;
      });
    },
    //
    handleDel: function(row) {
      this.$confirm("?", "", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          //NProgress.start();
          let para = { id: row.id };
          removeRadio(para).then(res => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
              message: "",
              type: "success"
            });
            this.getRadios();
          });
        })
        .catch(() => {});
    },
    //
    handleEdit: function(row) {
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
    },
    //
    handleAdd: function() {
      this.addFormVisible = true;
      this.addForm = {
        title: "",
        author: "",
        url: "",
        duration: "",
        upload_time: "",
        praise_number: "",
        play_number: "",
        img_url: "",
        article_author: "",
        article_content: "",
        article_remark: "",
      };
    },
    //
    editSubmit: function() {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.editLoading = true;
            //NProgress.start();
            let para = Object.assign({}, this.editForm);
            console.log("editSubmit para", para);
            editRadio(para).then(res => {
              this.editLoading = false;
              //NProgress.done();
              this.$message({
                message: "",
                type: "success"
              });
              this.$refs["editForm"].resetFields();
              this.editFormVisible = false;
              this.getRadios();
            });
          });
        }
      });
    },
    //
    addSubmit: function() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.addLoading = true;
            //NProgress.start();
            let para = Object.assign({}, this.addForm);
            console.log("addSubmit para", para);
            addRadio(para).then(res => {
              this.addLoading = false;
              //NProgress.done();
              this.$message({
                message: "",
                type: "success"
              });
              this.$refs["addForm"].resetFields();
              this.addFormVisible = false;
              this.getRadios();
            });
          });
        }
      });
    },
    selsChange: function(sels) {
      this.sels = sels;
    },
    //
    batchRemove: function() {
      // var ids = this.sels.map(item => item.id).toString();
      var ids = this.sels.map(item => item.id);
      this.$confirm("", "", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          //NProgress.start();
          let para = { ids: ids };
          batchRemoveRadio(para).then(res => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
              message: "",
              type: "success"
            });
            this.getRadios();
          });
        })
        .catch(() => {});
    }
  },
  mounted() {
    var login_user = sessionStorage.getItem('user');
    if (login_user) {
        this.login_user = JSON.parse(login_user);
    }

    this.getRadios();
  }
};
</script>

<style scoped>

</style>


